<template>
  <div>
    <h1></h1>
    <form @submit.prevent="POST_create">
        <select v-model="tag_pk" >
          <option :value="1"></option>
          <option :value="2"></option>
          <option :value="3"></option>
          <option :value="4"></option>
        </select>
        <div>
          <label for="new-article-title">: </label>
          <input type="text" id="new-article-title" placeholder="" v-model="title">
        </div>
        <div>
          <label for="new-article-conent">: </label>
          <input type="text" id="new-article-conent" placeholder="" v-model="content">
          <input type="file" accept="image/*" @change="fileChange" />
        </div>
        <input type="submit">
    </form>
  </div>
</template>

<script>
export default {
  name: "CreateArticleView",
  data(){
    return {
      title: '',
      content: '',
      tag_pk: 1,
      profile_image: null,
    }
  },
  methods: {
    fileChange: function(e) {
      const file = e.target.files;
    let validation = true;
    let message = '';

    if (file.length > 1) {
        validation= false;
        message = `   .`
    }

    if (file[0].size > 1024 * 1024 * 2) {
        message = `${message},   2MB  .`;
        validation = false;
    }

    if (file[0].type.indexOf('image') < 0) {
        message = `${message},    .`;
        validation = false;
    }

    if (validation) {
        this.profile_image = file
    }else {
        this.profile_image = '';
        alert(message);
    }
    console.log(this.profile_image)
    },
    async POST_create(){
      // {title, content, tag_pk, by, key}
      // await this.$store.dispatch("POST_create", {title: " ", content: "", tag_pk: 1, by:"id", key:502356})
      const {title, content, tag_pk, profile_image} = this
      const {by, key} = this.$route.params
      const {id} = await this.$store.dispatch("POST_create", {title, content, tag_pk, by, key})
      const res = await this.$store.dispatch("POST_image", {profile_image})
      console.log(res)
      this.$router.push({ name: 'articleDetail', params: {articlePk:id} })
    }
  }

}
</script>

